:root {
  /* --bg-color: #c0c0c0; */

  /* --bg-color: #36454f; */
  /* --bg-color: #000022; */
  --bg-color: #22324b;
  --accent-text: #e4c9a2;
}

.header {
  /* background: #000022; */
  /* position: relative; */
  padding-top: 10px;
  padding-bottom: 10px;
  /* background-color: #36454f; */
  background-color: var(--bg-color);
  z-index: 1;
  padding-bottom: 30px;
}

.header-image-logo {
  border-radius: 50%;
  margin-left: 20px;
}

.header-text {
  /* margin: 0; */
  color: var(--accent-text);
  /* margin-left: 20px; */
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 20.5px;
}

.header-logo {
  font-size: 60px;
  color: var(--accent-text);
  /* margin-left: 20px; */
  font-weight: 700;
  text-transform: uppercase;
}

.header-wrap {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
}

.header-menu {
  display: flex;
  list-style: none;
  padding: 0;
  gap: 20px;
}

.hero {
  padding-top: 20px;
  padding-bottom: 20px;
  /* position: relative; */
  /* background-color: #36454f; */
  background-color: var(--bg-color);
}

.hero-wrap {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 25px;
}

.hero-img {
  text-align: center;
  /* border-radius: 370px;
  border-top-right-radius: 365px;
  border-bottom-left-radius: 376px; */
  /* border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%; */
  border-radius: 49% 51% 55% 45% / 30% 27% 73% 70%;
}

.hero-content {
  /* border-radius: 12px;
  border: 1px solid wheat;
  backdrop-filter: blur(30px);
  max-width: 300px; */
  padding: 20px;
}

.hero-title-0 {
  /* margin: 0; */
  text-align: center;
  color: white;
  font-size: 26px;
  text-transform: uppercase;
}

.hero-title {
  font-size: 19px;
  color: white;
  margin-top: 0;
  text-transform: uppercase;
}

.hero-btn {
  /* width: 100%; */
  padding: 10px;
  background-color: var(--accent-text);
  text-transform: uppercase;
}

.btn {
  padding-top: 11.5px;
  padding-bottom: 11.5px;
}

.btn-2 {
  padding-right: 22px;
  padding-left: 22px;
}
